import {
  CanActivateFn,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  Router
} from '@angular/router';
import { inject } from '@angular/core';
import { map, catchError } from 'rxjs/operators';
import { of } from 'rxjs';
import { AuthService } from '../services/auth-service';

export const authGuard: CanActivateFn = (
  next: ActivatedRouteSnapshot,
  state: RouterStateSnapshot
) => {
  const authService = inject(AuthService);
  const router = inject(Router);

  return authService.authenticate().pipe(
    map((response) => {
      // Vrifier si l'utilisateur est bloqu
      if (authService.isUserBlocked() && state.url !== '/user-blocked') {
        router.navigate(['/access-denied']);
        return false;
      }

      // Si l'utilisateur n'est pas authentifi
      if (!response.status) {
        router.navigate(['/login']);
        return false;
      }

      // Initialiser les donnes utilisateur
      if (response.user) {
        authService.user = response.user;
        const roles = authService.getRolesNames() as string[];
        authService.getMenuByRole(roles);
      }

      // Grer la vrification OTP
      const requiresOtpVerification =
        response.otp_required &&
        !response.status_otp &&
        !response.otp_status_auth;

      if (requiresOtpVerification) {
        authService.response = response;
        router.navigate(['otp']);
        return false;
      }

      return true;
    }),
    catchError((error) => {
      console.error("Erreur d'authentification:", error);
      router.navigate(['/login']);
      return of(false);
    })
  );
};
