import {
  Component,
  ViewChild,
  AfterViewInit,
  inject,
  ChangeDetectorRef,
  OnInit,
  SimpleChanges,
  OnChanges
} from '@angular/core';
import { FieldType, FieldTypeConfig, FormlyFieldConfig, FormlyModule } from '@ngx-formly/core';
import { FilePondComponent, FilePondModule } from 'ngx-filepond';
import { FilePondOptions } from 'filepond';
import FilePondPluginImageExifOrientation from 'filepond-plugin-image-exif-orientation';
import * as FilePond from 'filepond';
import FilePondPluginFileValidateType from 'filepond-plugin-file-validate-type';
import FilePondPluginImagePreview from 'filepond-plugin-image-preview';
import { FilepondOptionsConfig } from 'src/app/interfaces/FilepondOptionsConfig';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { MatSnackBar, MatSnackBarModule } from '@angular/material/snack-bar';
import { CommonModule, NgFor, NgForOf } from '@angular/common';

FilePond.registerPlugin(
  FilePondPluginFileValidateType,
  FilePondPluginImagePreview,
  FilePondPluginImageExifOrientation
);
@Component({
  selector: 'vex-formly-field-filepond',
  standalone: true,
  imports: [CommonModule, FormlyModule, FilePondModule],
  template: `
    <!-- On passe les options depuis les props et on coute les vnements -->
    <file-pond
  #pond
  [options]="getPondOptionsForField(field)"
  (onaddfile)="onFileAdded($event)"
  (onremovefile)="onFileRemoved()">
</file-pond>

  `
})
export class FormlyFieldFilepondComponent
  extends FieldType<FieldTypeConfig>
  implements OnInit
{
  @ViewChild('pond') pondComponent?: FilePondComponent;
  private cd = inject(ChangeDetectorRef);
  // Les options par dfaut peuvent tre surcharges par les props
  pondFiles: File[] = [];
  pondOptionsConfig: FilePond.FilePondOptions = FilepondOptionsConfig;
  maxTotalSize: number = 10 * 1024 * 1024; // 10 MB
  fileFields: any[] = []; // Ajout pour le *ngFor

  constructor(
    private translate: TranslateService
  ) {
    super();
    this.fileFields = [
      {
        key: 'file' // Cl correspondant au champ dans le formulaire
      }
    ];
  }
  ngOnInit() {}

getPondOptionsForField(field: FormlyFieldConfig): FilePondOptions {
  const key = Array.isArray(field.key) ? field.key.join('.') : String(field.key ?? 'file');

  const pondOptions: FilePondOptions = {
    ...this.pondOptionsConfig,
    name: key,
    credits: false,
    allowReorder: true,
    allowImagePreview: true,
    imagePreviewHeight: 170,
    labelIdle: this.translate.instant('global.filepond.label_idle'),
  };
  return pondOptions;
}

  // Quand un fichier est ajout dans FilePond...
  onFileAdded(event: any) {
    const file = event.file.file; // On rcupre l'objet File natif
    // ...on met  jour la valeur du contrle de formulaire Formly.
    this.formControl.setValue(file);
    this.cd.detectChanges();
  }

  // Quand un fichier est retir...
  onFileRemoved() {
    this.formControl.setValue(null);
    this.pondFiles = [];
    this.cd.detectChanges();
  }
}
