import { inject } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivateFn, Router, RouterStateSnapshot } from '@angular/router';
import { map, catchError, of, switchMap } from 'rxjs';
import { AuthService } from '../services/auth-service';
import { Authority } from 'src/static-data/authority.constants';
import { AdminAuthService } from '../services/admin-auth.service';

export const maintenanceGuard: CanActivateFn = (_route: ActivatedRouteSnapshot, state: RouterStateSnapshot) => {
  const adminAuthService = inject(AdminAuthService);
  const authService = inject(AuthService);
  const router = inject(Router);

  // Routes toujours accessibles sans vrification
  const alwaysAccessiblePaths = [
    '/login/admin',
    '/admin/maintenance'
  ];

  // Si la route est toujours accessible, laisser passer
  if (alwaysAccessiblePaths.some((path) => state.url.startsWith(path))) {
    return true;
  }

  // Fonction pour vrifier si l'utilisateur est admin
  const isAdmin = (): boolean => {
    const user = authService.user;
    if (!user || !user.roles) return false;
    return user.roles.some(role => role.name === Authority.ADMIN);
  };

  // Si c'est la page de maintenance, vrifier si l'admin est connect
  if (state.url.startsWith('/maintenance-mode')) {
    // Vrification immdiate en mmoire
    if (isAdmin()) {
      router.navigate(['/index']);
      return false;
    }

    // Vrification avec le serveur
    return authService.authenticate().pipe(
      map((response) => {
        const user = response.user;
        if (user && user.roles?.some(role => role.name === Authority.ADMIN)) {
          // Admin connect : rediriger vers /index
          router.navigate(['/index']);
          return false;
        }
        // Utilisateur normal : laisser accder  la page de maintenance
        return true;
      }),
      catchError(() => {
        // En cas d'erreur, laisser accder  la page de maintenance
        return of(true);
      })
    );
  }

  return adminAuthService.checkMaintenanceStatus().pipe(
    switchMap((status) => {
      if (status.is_active) {
        // Vrification immdiate en mmoire pour l'admin
        if (isAdmin()) {
          // Admin dj authentifi : laisser passer immdiatement
          return of(true);
        }

        // Vrifier avec le serveur si l'utilisateur est un admin authentifi
        return authService.authenticate().pipe(
          map((response) => {
            const user = response.user;
            if (user && user.roles?.some(role => role.name === Authority.ADMIN)) {
              // Admin authentifi : laisser passer vers /index
              return true;
            } else {
              // Utilisateur normal : rediriger vers la page de maintenance
              router.navigate(['/maintenance-mode']);
              return false;
            }
          }),
          catchError(() => {
            // En cas d'erreur d'authentification, considrer comme utilisateur normal
            router.navigate(['/maintenance-mode']);
            return of(false);
          })
        );
      }
      // Si le mode maintenance n'est pas actif, laisser passer
      return of(true);
    }),
    catchError(() => {
      // En cas d'erreur, laisser passer
      return of(true);
    })
  );
};
