import { Component, Inject, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MAT_DIALOG_DATA, MatDialogModule, MatDialogRef } from '@angular/material/dialog';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { FormlyFieldConfig, FormlyModule } from '@ngx-formly/core';
import { FormlyMaterialModule } from '@ngx-formly/material';
import { MatButtonModule } from '@angular/material/button';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { tasksFormlyFields } from 'src/app/interfaces/Tasks';
import { Task } from 'src/app/models/task.model';

@Component({
  selector: 'vex-task-create-update',
  standalone: true,
  imports: [
    CommonModule,
    MatDialogModule,
    MatButtonModule,
    ReactiveFormsModule,
    FormlyModule,
    FormlyMaterialModule,
    TranslateModule
  ],
  templateUrl: './task-create-update.component.html',
  styleUrls: ['./task-create-update.component.scss']
})
export class TaskCreateUpdateComponent implements OnInit {
  form = new FormGroup({});
  model: any = {};
  fields: FormlyFieldConfig[] = [];
  mode: 'create' | 'update' = 'create';

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: { mode: 'create' | 'update'; task?: Task },
    private dialogRef: MatDialogRef<TaskCreateUpdateComponent>,
    private translate: TranslateService
  ) {
    this.mode = data.mode;

    if (this.mode === 'update' && data.task) {
      this.model = { ...data.task };
    }
  }

  ngOnInit(): void {
    // Translate field labels, placeholders and validation messages
    this.fields = tasksFormlyFields.map(field => ({
      ...field,
      props: {
        ...field.props,
        label: this.translate.instant(field.props?.label || ''),
        placeholder: this.translate.instant(field.props?.placeholder || '')
      },
      validation: field.validation ? {
        messages: Object.keys(field.validation.messages || {}).reduce((acc, key) => {
          const message = field.validation!.messages![key];
          acc[key] = typeof message === 'string' ? this.translate.instant(message) : message;
          return acc;
        }, {} as any)
      } : undefined
    }));
  }

  save(): void {
    if (this.form.valid) {
      console.log('[TaskCreateUpdate] Saving model:', this.model);
      console.log('[TaskCreateUpdate] Profile field:', this.model.profile);
      console.log('[TaskCreateUpdate] Profile is array?', Array.isArray(this.model.profile));
      if (Array.isArray(this.model.profile)) {
        console.log('[TaskCreateUpdate] Profile files:', this.model.profile.map((f: any) => ({
          name: f.name,
          size: f.size,
          type: f.type
        })));
      }
      this.dialogRef.close({ model: this.model });
    }
  }

  cancel(): void {
    this.dialogRef.close();
  }
}
